.comments_nav__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

html {
  font-size: 10px;
}

html,
body {
  height: 100%;
  font-weight: 400;
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  color: #2E3E5C;
}

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}

.main {
  flex-grow: 1;
}

.section {
  width: 100%;
  padding: 10rem 0;
}
.section.section--bg {
  background-color: rgba(112, 111, 229, 0.02);
}

.section__title {
  font-size: 4rem;
  font-weight: 800;
  line-height: 1.5;
  color: #1e1f4b;
}
.section__title.section__title--centr {
  text-align: center;
}

.container {
  max-width: 114.4rem;
  width: 100%;
  padding: 0 2rem;
  margin: 0 auto;
}

.comments {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 7rem;
}

.comments_title {
  margin-bottom: 11rem;
}

.comments__item {
  display: block;
  max-width: 67rem;
  width: 100%;
}

.comments__item_text {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  color: #03014C;
}

.comments_nav {
  display: flex;
}

.comments_nav__img_w {
  border-radius: 50%;
  overflow: hidden;
  height: 8.6rem;
  width: 8.6rem;
}